.header {
    display: flex;
    justify-content: space-between; /* 왼쪽과 오른쪽 정렬 */
    align-items: center;
    padding: 20px 50px;
    background-color: #141414;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    box-sizing: border-box; /* 패딩 포함하여 크기 계산 */
  }
  
  .header__nav {
    display: flex;
    flex: 1; /* 네비게이션 바가 전체 너비 차지 */
    justify-content: space-around; /* 버튼들 사이 간격 균등 */
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .header__nav li {
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    color: white;
    transition: color 0.3s ease;
  }
  
  .header__nav li:hover {
    color: #e50914; /* Netflix 빨간색으로 호버 시 강조 */
  }
  
  .header__logo {
    height: 35px;
    cursor: pointer;
  }
  
  .header__profile {
    display: flex;
    align-items: center;
    justify-content: center; /* 수평 중앙 정렬 */
    position: relative; /* 필요시 위치 조정 */
  }
  
  .header__avatar {
    width: 40px; /* 이미지 크기 조정 */
    height: 40px;
    border-radius: 50%; /* 둥근 이미지 */
    cursor: pointer;
    object-fit: cover; /* 이미지 왜곡 방지 */
    z-index: 1; /* 다른 요소 위로 배치 */
  }
  
  /* 문제 발생 가능성 해결을 위해 추가 */
  .header__profile img {
    display: block; /* 이미지가 inline으로 간주되지 않도록 설정 */
  }
  
  .header__nav a {
    text-decoration: none; /* 밑줄 제거 */
    color: inherit; /* 부모 요소의 색상 사용 */
  }
  
  .header__nav a:hover {
    text-decoration: none; /* 호버 시에도 밑줄 제거 */
    color: inherit; /* 호버 시에도 색상 변경 없음 */
  }
/* 로그아웃 모달 */
.logoutModal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* 배경 흐리게 처리 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* 팝업이 다른 내용 위에 표시되도록 */
}

.logoutModal__content {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.logoutModal__content h3 {
  font-size: 18px;
  margin-bottom: 20px;
}

.logoutModal__content button {
  padding: 10px 20px;
  margin: 5px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.logoutModal__content button:hover {
  background-color: #555;
}