.popular-content {
    padding: 20px;
    background-color: #141414;
    color: white;
  }
  
  .popular-content h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .popular-content__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
  }
  
  .popular-content__item {
    text-align: center;
  }
  
  .popular-content__item img {
    width: 100%;
    height: 225px;
    object-fit: cover;
    border-radius: 10px;
    transition: transform 0.3s ease;
  }
  
  .popular-content__item img:hover {
    transform: scale(1.05);
  }
  