.wishlist {
    padding: 20px;
    background-color: #141414; /* 배경색 검정으로 설정 */
    color: #fff;
    min-height: 100vh; /* 페이지가 짧아도 배경이 전체 화면을 채우도록 설정 */
  }
  
  .wishlist h1 {
    font-size: 2rem;
    margin-bottom: 20px;
    text-align: center;
    color: #e5e5e5;
  }
  
  .wishlist__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    justify-content: center;
  }
  
  .wishlist__card {
    background-color: #222;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .wishlist__card:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
  }
  
  .wishlist__image {
    width: 100%;
    height: 225px;
    object-fit: cover;
    border-bottom: 2px solid #333;
  }
  
  .wishlist__title {
    padding: 10px;
    font-size: 1rem;
    color: #fff;
    text-align: center;
  }
  
  @media (max-width: 768px) {
    .wishlist__grid {
      grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    }
  
    .wishlist__image {
      height: 180px;
    }
  
    .wishlist__title {
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 480px) {
    .wishlist__grid {
      grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    }
  
    .wishlist__image {
      height: 150px;
    }
  
    .wishlist__title {
      font-size: 0.8rem;
    }
  }
  